:root {
  --color-blue: #0205c7;
  --color-red: #f00109;
  --color-green: #00e676;

  --color-purple: #8297e0;

  --color-background-gray: #e5e5e5;

  --header-height: 61px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Poppins", sans-serif;
  margin-top: 61px;
  margin-top: var(--header-height);
}

li {
  list-style-type: none;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

.section {
  text-align: center;
  padding: 4.5rem 0;
}

.mt-1 {
  margin-top: 1rem;
}

.mt-2 {
  margin-top: 2rem;
}

.bg-gray {
  background-color: #e5e5e5;
  background-color: var(--color-background-gray);
}

.bg-white {
  background-color: white;
}

@media screen and (min-width: 1024px) {
  :root {
    --header-height: 80px;
  }
  .section {
    padding: 5.5rem 0;
  }
}

