.stat-item {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 15rem;
}

.stat-item__number {
  font-size: 72px;
  font-weight: 700;
  line-height: 1;
  color: var(--color-blue);
}

.stat-item__caption {
  font-size: 18px;
  font-weight: 500;
  width: 60%;
}

@media screen and (min-width: 1024px) {
  .stat-item__caption {
    font-size: 18px;
    font-weight: 500;
    width: 90%;
  }
}